import React, { useState } from "react";
import styles from "./reset-password.module.css";
import {
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { request } from "../../utils/request";
import { useNavigate, Link } from "react-router-dom";
import { useAppDispatch } from "../../services/store";
import { denyResetPassword } from "../../services/slices/resetPasswordSlice";

export function ResetPasswordPage() {
  const [password, setPassword] = useState("");
  const [token, setToken] = useState("");
  const navigate = useNavigate();
  const dispatch = useAppDispatch();

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    request("password-reset/reset", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ password, token }),
    })
      .then((data) => {
        console.log(" /password-reset/reset:", data);
        dispatch(denyResetPassword());
        navigate("/login");
      })
      .catch((err) => console.error(":", err));
  };

  return (
    <div className={styles.page}>
      <form className={styles.form} onSubmit={handleSubmit}>
        <h1 className='text text_type_main-medium'> </h1>

        <Input
          type="password"
          placeholder="  "
          value={password}
          name="password"
          onChange={(e) => setPassword(e.target.value)}
          onPointerEnterCapture={() => {}}
          onPointerLeaveCapture={() => {}}
        />

        <Input
          type="text"
          placeholder="   "
          value={token}
          name="token"
          onChange={(e) => setToken(e.target.value)}
          onPointerEnterCapture={() => {}}
          onPointerLeaveCapture={() => {}}
        />

        <Button htmlType="submit"></Button>

        <p className="text mt-20 text_type_main-default">
           ? <Link to="/login" className="text_color_accent"></Link>
        </p>
      </form>
    </div>
  );
}
