import { useState } from "react";
import {
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, useNavigate } from "react-router-dom";
import styles from "./login.module.css";
import { useAppDispatch } from "../../services/store";
import { loginUser } from "../../services/slices/authSlice";
import { useLocation } from "react-router-dom";

export function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const location = useLocation();

  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const from = (location.state as { from?: Location })?.from?.pathname || "/";

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const result = await dispatch(loginUser({ email, password })).unwrap();
      navigate(from, { replace: true });
    } catch (err: unknown) {
      const message =
        err instanceof Error
          ? err.message
          : typeof err === "string"
          ? err
          : " ";

      console.error(" :", err);
      setError(message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className={styles.page}>
      <form className={styles.form} onSubmit={handleSubmit}>
        <h2 className="text text_type_main-medium center"></h2>

        {error && (
          <p className="text text_type_main-default text_color_error center">
               
          </p>
        )}

        <Input
          type="email"
          placeholder="E-mail"
          value={email}
          name="email"
          onChange={(e) => setEmail(e.target.value)}
          onPointerEnterCapture={() => {}}
          onPointerLeaveCapture={() => {}}
        />

        <Input
          type="password"
          placeholder=""
          value={password}
          name="password"
          onChange={(e) => setPassword(e.target.value)}
          onPointerEnterCapture={() => {}}
          onPointerLeaveCapture={() => {}}
        />

        <Button
          htmlType="submit"
          type="primary"
          size="medium"
          extraClass="mb-20"
          disabled={loading}
        >
          {loading ? "..." : ""}
        </Button>

        <p className="text text_type_main-default text_color_inactive center">
             ?{" "}
          <Link to="/register" className="text_color_accent">
            
          </Link>
        </p>

        <p className="text text_type_main-default text_color_inactive center">
           ?{" "}
          <Link to="/forgot-password" className="text_color_accent">
             
          </Link>
        </p>
      </form>
    </div>
  );
}
